"use client"
import { useEffect, useState } from 'react'

export default function AdminTasksPage() {
  const [title, setTitle] = useState('')
  const [points, setPoints] = useState(10)
  const [tasks, setTasks] = useState<{id:number; title:string; points:number}[]>([])

  const load = async () => {
    const r = await fetch('/api/tasks')
    const d = await r.json()
    setTasks(d.tasks || [])
  }
  useEffect(() => { load() }, [])

  const create = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!title) return
    await fetch('/api/tasks', { method: 'POST', headers: { 'Content-Type':'application/json' }, body: JSON.stringify({ title, points })})
    setTitle('')
    setPoints(10)
    load()
  }

  return (
    <div className="p-6 space-y-4">
      <h1 className="text-xl font-semibold">Aufgaben</h1>
      <form onSubmit={create} className="flex gap-2 items-end">
        <div>
          <label className="block text-sm">Titel</label>
          <input className="border px-2 py-1" value={title} onChange={e=>setTitle(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">Punkte</label>
          <input type="number" className="border px-2 py-1 w-24" value={points} onChange={e=>setPoints(parseInt(e.target.value||'0'))} />
        </div>
        <button className="bg-black text-white px-3 py-1">Erstellen</button>
      </form>

      <ul className="space-y-2">
        {tasks.map(t => (
          <li key={t.id} className="border p-2 rounded">
            <div className="font-medium">{t.title}</div>
            <div className="text-sm text-gray-600">{t.points} Punkte</div>
          </li>
        ))}
      </ul>
    </div>
  )
}
